import React, { useCallback, useContext, useState } from "react";
import { LoadingButton } from "@mui/lab";
import {
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  Stack,
  TextField,
  Autocomplete,
  Button,
  Typography,
  Alert,
  IconButton,
  Grid,
  Divider,
  useTheme,
} from "@mui/material";
import _ from "lodash";
import Swal from "sweetalert2";
import { Formik } from "formik";
import CourseItem from "../CourseItem";
import { CloseSharp } from "@mui/icons-material";
import { string, array, object } from "yup";
import { useQuery, useMutation, useQueryClient } from "react-query";
import { CustomContext } from "../context/provider/CustomProvider";
import { addNewRegisteredCourses } from "../api/registeredCoursesApi";
import { getAllAssignedCoursesProgramme } from "../api/assignedCourseApi";

function AddNewCourse({ open, setOpen }) {
  const queryClient = useQueryClient();
  const { customState } = useContext(CustomContext);
  const { palette } = useTheme();

  const [academicOptions] = useState(["2021", "2022"]);
  const [programmeOptions] = useState([customState?.studentInfo?.programme]);
  const [levelOptions] = useState(["100", "200", "300", "400"]);
  const [semesterOptions] = useState(["1", "2"]);
  const [coursesOptions, setCourseOptions] = useState([]);

  const [academicYear, setAcademicYear] = useState(null);
  const [semester, setSemester] = useState(null);
  const [programme, setProgramme] = useState(null);
  const [level, setLevel] = useState(null);
  const [course, setCourse] = useState(null);
  const [creditHours, setCreditHours] = useState(0);
  const [courses, setCourses] = useState([]);
  const [courseError, setCourseError] = useState({ severity: "", msg: "" });
  const [programmeError, setProgrammeError] = useState("");

  useQuery(
    ["allCoursesByProgramme", programme],
    () => getAllAssignedCoursesProgramme(programme),
    {
      enabled: !!programme,
      onSuccess: (courses) => {
        const registeredCourses = courses.map((course) => {
          return {
            id: course.course.id,
            label: course.course.label,
            professionalID: course.professionalID,
          };
        });
        setProgrammeError("");

        setCourseOptions(registeredCourses);
      },
      onError: (error) => {
        setProgrammeError(error);
      },
    }
  );

  const initialValues = {
    indexNumber: customState?.studentInfo?.indexNumber,
    academicYear,
    programme,
    level,
    semester,
    courses,
  };

  const validationSchema = object().shape({
    academicYear: string().required().nullable(true),
    semester: string().required().nullable(true),
    programme: string().required().nullable(true),
    level: string().required().nullable(true),
    courses: array().required().min(1, "Courses cannot be empty"),
  });

  const registeredCoursesMutate = useMutation(addNewRegisteredCourses);

  const onSubmit = (values, options) => {
    // //values);
    setCourseError({ severity: "", msg: "" });
    if (_.isEmpty(values)) {
      setCourseError({
        severity: "info",
        msg: "Please select a course to register!!!",
      });
      return;
    }
    options.setSubmitting(true);
    registeredCoursesMutate.mutateAsync(values, {
      onSettled: () => {
        queryClient.invalidateQueries(["studentCourse"]);
        options.setSubmitting(false);
      },
      onSuccess: (data) => {
        Swal.fire({
          icon: "success",
          iconColor: palette.primary.main,
          title: "Success",
          text: "Course registration successful !!!",
          confirmButtonColor: palette.primary.main,
        });
        setCourseError({
          severity: "success",
          msg: "Course registration successful !!!",
        });
      },
      onError: (error) => {
        Swal.fire({
          icon: "error",
          iconColor: palette.primary.main,
          title: "Error",
          text: "Error registering courses.Please try again",
          confirmButtonColor: palette.primary.main,
        });
      },
    });
  };

  const handleAddCourse = useCallback(() => {
    setCourseError({ severity: "", msg: "" });

    const any = courses.find((oldCourse) => oldCourse.course.id === course.id);
    if (any) {
      setCourseError({ severity: "error", msg: "Course already exists" });
      return;
    }
    setCourseError({ severity: "", msg: "" });

    setCourses([
      ...courses,
      {
        course,
        creditHours,
      },
    ]);
  }, [courses, course, creditHours]);

  const handleRemoveCourse = (id) => {
    const filteredCourses = courses.filter(
      (oldCourse) => oldCourse.course.id !== id
    );
    setCourses(filteredCourses);
  };
  const handleCloseDialog = () => {
    setOpen(false);
    setCourseError({
      severity: "",
      msg: "",
    });
  };

  return (
    <Dialog open={open} onClose={handleCloseDialog} fullWidth maxWidth="sm">
      <DialogTitle display="flex" justifyContent="space-between">
        <Typography>Student Course Registration</Typography>
        <IconButton onClick={handleCloseDialog}>
          <CloseSharp />
        </IconButton>
      </DialogTitle>

      <Formik
        initialValues={initialValues}
        onSubmit={onSubmit}
        enableReinitialize={true}
        validationSchema={validationSchema}
      >
        {({
          values,
          errors,
          touched,
          handleSubmit,
          handleChange,
          isSubmitting,
        }) => {
          return (
            <>
              <DialogContent>
                {courseError.msg && (
                  <Alert
                    severity={courseError.severity}
                    onClose={() => setCourseError({ severity: "", msg: "" })}
                  >
                    {courseError.msg}
                  </Alert>
                )}

                <Typography>1.Academic Information</Typography>
                <Divider />

                <Grid container spacing={3} paddingY={4}>
                  <Grid item xs={12} md={6}>
                    <Autocomplete
                      disablePortal
                      options={academicOptions}
                      value={academicYear}
                      onChange={(e, value) => setAcademicYear(value)}
                      renderInput={(props) => (
                        <TextField
                          required
                          {...props}
                          label="Academic Year"
                          
                          fullWidth
                          size="small"
                          error={Boolean(
                            touched.academicYear && errors.academicYear
                          )}
                          helperText={errors.academicYear}
                        />
                      )}
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Autocomplete
                      disablePortal
                      options={programmeOptions}
                      value={programme}
                      onChange={(e, value) => setProgramme(value)}
                      renderInput={(props) => (
                        <TextField
                          required
                          {...props}
                          label="Programme"
                          
                          fullWidth
                          size="small"
                          error={Boolean(touched.programme && errors.programme)}
                          helperText={errors.programme}
                        />
                      )}
                    />
                    <small style={{ color: "red" }}>{programmeError}</small>
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Autocomplete
                      disablePortal
                      options={levelOptions}
                      value={level}
                      onChange={(e, value) => setLevel(value)}
                      renderInput={(props) => (
                        <TextField
                          required
                          {...props}
                          label="Level"
                          
                          fullWidth
                          size="small"
                          error={Boolean(touched.level && errors.level)}
                          helperText={errors.level}
                        />
                      )}
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Autocomplete
                      disablePortal
                      options={semesterOptions}
                      value={semester}
                      onChange={(e, value) => setSemester(value)}
                      renderInput={(props) => (
                        <TextField
                          required
                          {...props}
                          
                          label="Semester"
                          fullWidth
                          size="small"
                          error={Boolean(touched.semester && errors.semester)}
                          helperText={errors.semester}
                        />
                      )}
                    />
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <Autocomplete
                      disablePortal
                      options={coursesOptions}
                      value={course}
                      isOptionEqualToValue={(option, value) =>
                        option.label === value.label
                      }
                      onChange={(e, value) => setCourse(value)}
                      renderInput={(props) => (
                        <TextField
                          required
                          {...props}
                          
                          label="Course"
                          fullWidth
                          size="small"
                          error={Boolean(touched.courses && errors.courses)}
                          helperText={errors.courses}
                        />
                      )}
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <TextField
                      required
                      
                      label="Credit Hours"
                      fullWidth
                      size="small"
                      type="number"
                      value={creditHours}
                      onChange={(e) => setCreditHours(e.currentTarget.value)}
                      // error={Boolean(touched.creditHours && errors.creditHours)}
                      //   helperText={errors.creditHours}
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Button
                      variant="contained"
                      disabled={
                        course === null ||
                        Boolean(creditHours === null || creditHours === 0)
                      }
                      onClick={handleAddCourse}
                    >
                      Add Course
                    </Button>
                  </Grid>
                </Grid>

                <Typography>2.Selected Courses</Typography>
                <Divider />
                <Stack direction="row" spacing={2} paddingY={2}>
                  {courses.map((course, index) => {
                    return (
                      <CourseItem
                        key={index}
                        course={course.course}
                        hrs={course.creditHours}
                        handleRemove={handleRemoveCourse}
                      />
                    );
                  })}
                </Stack>
              </DialogContent>
              <DialogActions>
                <LoadingButton
                  loading={isSubmitting}
                  variant="contained"
                  onClick={handleSubmit}
                >
                  Register Courses
                </LoadingButton>
                <Button color="secondary" onClick={() => setOpen(false)}>
                  Cancel
                </Button>
              </DialogActions>
            </>
          );
        }}
      </Formik>
    </Dialog>
  );
}

export default AddNewCourse;
