import { Button, Stack, Typography, Divider } from "@mui/material";
import React, { useContext, useState } from "react";
import RegisteredCourseTable from "../tables/RegisteredCourseTable";
import { AddSharp } from "@mui/icons-material";
import AddNewCourse from "../modals/AddNewCourse";
import { CustomContext } from "../context/provider/CustomProvider";

function CourseRegistrationTab() {
  
  const [openAddNewCourse, setOpenAddNewCourse] = useState(false);
  const { customState } = useContext(CustomContext);
  return (
    <section>
      <Stack
        paddingBottom={1}
        direction="row"
        justifyContent="space-between"
        alignItems="center"
      >
        <Typography>Registered Courses</Typography>
        {/* {customState.user.role === "administrator" && (
          <Button
            variant="contained"
            color="secondary"
            startIcon={<AddSharp />}
            onClick={() => setOpenAddNewCourse(true)}
          >
            Register New Course
          </Button>
        )} */}
      </Stack>
      <Divider />
      <RegisteredCourseTable />

      <AddNewCourse open={openAddNewCourse} setOpen={setOpenAddNewCourse} />
    </section>
  );
}

export default CourseRegistrationTab;
