import React from "react";
import {
  Box,
  Typography,
  Divider,
  Stack,
  useTheme,
  Button,
} from "@mui/material";
import { DescriptionSharp } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { generateGradePoint } from "../../config";
import _ from "lodash";

function AcademicRecords({ studentResult, studentId }) {
  const { palette } = useTheme();
  const navigate = useNavigate();

  let creditHoursList = [];
  let gradePointsList = [];

  function generateGPA() {
    const totalGradePoint = _.sum(gradePointsList);
    const totalCreditHours = _.sum(creditHoursList);
    const f = totalGradePoint / totalCreditHours;
    return f.toFixed(2);
  }

  const infoData = studentResult?.map((result, index) => {
    return (
      <React.Fragment key={index}>
        <div>
          <Box
            sx={{
              backgroundColor: palette.primary.main,
              color: "#fff",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              padding: 1,
            }}
          >
            <p style={{ fontSize: "12px" }}>{result.year} ACADEMIC YEAR</p>
            <p style={{ fontSize: "12px" }}>LEVEL {result.level}</p>
            <p style={{ fontSize: "12px" }}>{result.semester} SEMESTER</p>
          </Box>
        </div>
        <table cellPadding="10px">
          <thead
            style={{
              backgroundColor: palette.secondary.main,
              color: "#fff",
              textAlign: "left",
            }}
          >
            <tr>
              <th>Code</th>
              <th>Course</th>
              <th>Credit Hours</th>
              <th>Score</th>
              <th>Grade</th>
              <th>Grade Point</th>
            </tr>
          </thead>
          <tbody style={{ backgroundColor: "whitesmoke" }}>
            {result.results.map((info, index) => {
              const gp = generateGradePoint(info.grade);
              creditHoursList.push(Number(info.creditHours));
              gradePointsList.push(Number(gp));

              return (
                <tr key={info.course.id}>
                  <td>{info.course.id}</td>
                  <td>{info.course.label}</td>
                  <td>{info.creditHours}</td>
                  <td>{info.score}</td>
                  <td>{info.grade}</td>
                  <td>{generateGradePoint(info.grade)}</td>
                </tr>
              );
            })}
          </tbody>
          <tfoot style={{ paddingBottom: "200px" }}>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td style={{ backgroundColor: "#8C1438", color: "#fff" }}>
                GPA-{generateGPA()}
              </td>
            </tr>
          </tfoot>
        </table>
      </React.Fragment>
    );
  });

  return (
    <Box>
      <Stack
        paddingBottom={1}
        direction="row"
        justifyContent="space-between"
        alignItems="center"
      >
        <Typography paddingBottom={1}>Academic Records</Typography>
        <Button
          variant="contained"
          color="secondary"
          disabled={studentResult.length === 0}
          onClick={() => {
            navigate("/info/preview", {
              replace: true,
            });
          }}
        >
          Preview
        </Button>
      </Stack>
      <Divider />
      <Stack paddingY={5} rowGap={1}>
        {studentResult.length === 0 ? (
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              paddingY: 8,
              gap: 2,
            }}
          >
            <DescriptionSharp
              sx={{
                width: 72,
                height: 72,
              }}
            />
            <Typography variant="h4">No Records Available</Typography>
          </Box>
        ) : (
          infoData
        )}
      </Stack>
    </Box>
  );
}

export default React.memo(AcademicRecords);
