import React from "react";

import {
  Stack,
  Typography,
  Box,
  Paper,
  Avatar,
  IconButton,
  FormLabel,
} from "@mui/material";
import { Edit } from "@mui/icons-material";

let BASE_URL;
if (process.env.NODE_ENV !== "production") {
  BASE_URL = process.env.REACT_APP_BASE_URL_LOCAL;
} else {
  BASE_URL = process.env.REACT_APP_BASE_URL;
}

function StudentPersonalInfoTab({ studentData }) {
  const pStyle = {
    fontSize: "13px",
  };

  return (
    <>
      <Box
        sx={{
          display: "flex",
          flexDirection: { xs: "column", md: "row" },
          justifyContent: "center",
          alignItems: { xs: "center", md: "flex-start" },
          gap: 5,
          paddingTop: 5,
        }}
      >
        <Box sx={{ position: "relative" }}>
          <Avatar
            src={
              studentData?.profile === "" || studentData?.profile === undefined
                ? null
                : `${BASE_URL}/images/${studentData?.profile}`
            }
            alt="profile"
            sx={{ maxWidth: 150, width: 150, height: 150 }}
          />

          <Stack alignItems="center" justifyContent="center" paddingBottom={3}>
            <Typography variant="h6">{studentData?.fullname}</Typography>
            <Typography>{studentData?.indexNumber}</Typography>
          </Stack>
          <IconButton
            sx={{
              position: "absolute",
              top: -5,
              right: -5,
              backgroundColor: "primary.main",
            }}
          >
            <FormLabel htmlFor="profile ">
              <Edit sx={{ color: "#fff" }} />
            </FormLabel>
          </IconButton>
          <input type="file" id="profile" hidden />
        </Box>
        {/* personal Information */}
        <Paper sx={{ padding: 3, borderTop: "2px solid #8C1438" }}>
          <table width="100%">
            <thead>
              <tr>
                <th colSpan="2" style={{ textAlign: "left", padding: "8px 0" }}>
                  <Typography variant="body1" color="primary">
                    Personal Info.
                  </Typography>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td style={pStyle}>Index Number</td>
                <td style={pStyle}>{studentData?.indexNumber}</td>
              </tr>
              <tr>
                <td style={pStyle}>Full Name</td>
                <td style={pStyle}>{studentData?.fullname}</td>
              </tr>

              <tr>
                <td style={pStyle}>Date of Birth</td>
                <td style={pStyle}>
                  {new Date(studentData?.dob).toLocaleDateString()}
                </td>
              </tr>
              <tr>
                <td style={pStyle}>Gender</td>
                <td style={pStyle}>{studentData?.gender}</td>
              </tr>
              <tr>
                <td style={pStyle}>Email Address</td>
                <td style={pStyle}>{studentData?.email}</td>
              </tr>
              <tr>
                <td style={pStyle}>Telephone Number</td>
                <td style={pStyle}>{studentData?.telephoneNo}</td>
              </tr>
            </tbody>
            <thead>
              <tr>
                <th
                  colSpan="2"
                  style={{ textAlign: "left", padding: "16px 0" }}
                >
                  <Typography variant="body1" color="primary">
                    Additional Info
                  </Typography>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td style={pStyle}>Department</td>
                <td style={pStyle}>{studentData?.department}</td>
              </tr>

              <tr>
                <td style={pStyle}>Programme</td>
                <td style={pStyle}>{studentData?.programme}</td>
              </tr>

              <tr>
                <td style={pStyle}>Current Level</td>
                <td style={pStyle}>{studentData?.level}</td>
              </tr>
            </tbody>
          </table>
        </Paper>
      </Box>
    </>
  );
}

export default StudentPersonalInfoTab;
