import React from "react";
import { Chip, Avatar } from "@mui/material";

const CourseItem = ({ course, handleRemove, hrs }) => {
  return (
    <Chip
      color="primary"
      avatar={<Avatar>{hrs}</Avatar>}
      sx={{ color: "#fff" }}
      label={course.label}
      onDelete={() => handleRemove(course.id)}
    />
  );
};

export default React.memo(CourseItem);
