import axios from "axios";

let BASE_URL;
if (process.env.NODE_ENV !== "production") {
  BASE_URL = process.env.REACT_APP_BASE_URL_LOCAL;
} else {
  BASE_URL = process.env.REACT_APP_BASE_URL;
}

const courseInstance = axios.create({
  baseURL: `${BASE_URL}/assigned_course`,
  timeout: 10000,
  headers: {
    "Content-Type": "application/json",
  },
});

export const getAllAssignedCourses = async () => {
  try {
    const res = await courseInstance.get();
    return res.data;
  } catch (error) {
    //error.message);
  }
};

export const getAllAssignedCoursesToLecturerByID = async (lecturerID) => {
  try {
    const res = await axios({
      baseURL: `${BASE_URL}/assigned_course/${lecturerID}`,
      method: "GET",
    });
    return res.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const getAllAssignedCoursesProgramme = async (programme) => {
  try {
    const res = await axios({
      baseURL: `${BASE_URL}/assigned_course/all/${programme}`,
      method: "GET",
    });
    return res.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const assignCourse = async (newCourse) => {
  try {
    const res = await axios({
      baseURL: `${BASE_URL}/assigned_course`,
      method: "POST",
      data: newCourse,
    });
    return res.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const updateAssignedCourse = async (updatedCourse) => {
  try {
    const res = await axios({
      baseURL: `${BASE_URL}/assigned_course`,
      method: "PUT",
      data: updatedCourse,
    });
    return res.data;
  } catch (error) {
    throw error.response.data;
  }
};

export const deleteAssignedCourse = async (ids) => {
  try {
    if (typeof ids === "string") {
      const res = await axios({
        baseURL: `${BASE_URL}/assigned_course`,
        method: "DELETE",
        params: {
          id: ids,
        },
      });

      return res.data;
    }

    if (typeof ids === "object") {
      let resArr = [];
      ids.map(async (id) => {
        const res = await axios({
          baseURL: `${BASE_URL}/assigned_course`,
          method: "DELETE",
          params: {
            id,
          },
        });

        resArr.push(res);
      });

      return resArr;
    }
  } catch (error) {
    throw error.response.data;
  }
};
