import React, { useContext } from "react";
import { Stack, Box, Typography, useTheme } from "@mui/material";
import { courseRecords } from "../data/rows";
import { CustomContext } from "../context/provider/CustomProvider";
import { DescriptionSharp } from "@mui/icons-material";

function RegisteredCourseTable() {
  const { palette } = useTheme();

  const { customState } = useContext(CustomContext);

  const infoData = customState?.studentCourse?.map((record, index) => {
    return (
      <React.Fragment key={record._id}>
        <div>
          <Box
            sx={{
              //   backgroundColor: palette.primary.main,
              //   color: "#fff",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              padding: 1,
            }}
          >
            {/* <p>{record.year} ACADEMIC YEAR</p> */}
            <p>LEVEL {record.level}</p>
            <p>{record.semester} SEMESTER</p>
          </Box>
        </div>
        <table cellPadding="10px">
          <thead
            style={{
              backgroundColor: palette.secondary.main,
              color: "#fff",
              textAlign: "left",
            }}
          >
            <tr>
              <th>Code</th>
              <th>Course</th>
              <th>Credit Hours</th>
            </tr>
          </thead>
          <tbody style={{ backgroundColor: "whitesmoke" }}>
            {record.courses.map((info, index) => {
              return (
                <tr key={index}>
                  <td width="20%">{info.id}</td>
                  <td width="50%"> {info.label}</td>
                  <td width="15%">{info.creditHours}</td>
                </tr>
              );
            })}
          </tbody>
          <tfoot style={{ paddingBottom: "200px" }}>
            <tr>
              <td>End</td>
            </tr>
          </tfoot>
        </table>
      </React.Fragment>
    );
  });

  return (
    <>
      <Box
        sx={{
          margin: "auto",
          marginTop: 2,
        }}
      >
        <Stack paddingY={3} rowGap={1}>
          {courseRecords.length === 0 ? (
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
                paddingY: 8,
                gap: 2,
              }}
            >
              <DescriptionSharp
                sx={{
                  width: 72,
                  height: 72,
                }}
              />
              <Typography variant="h4">No Registered Courses</Typography>
            </Box>
          ) : (
            infoData
          )}
        </Stack>
      </Box>
    </>
  );
}

export default RegisteredCourseTable;
