import React, { useContext, useState, useEffect } from "react";
import { Tab, Button } from "@mui/material";
import { TabContext, TabList, TabPanel } from "@mui/lab";
import StudentPersonalInfoTab from "../components/tabs/StudentPersonalInfoTab";
import AcademicRecords from "../components/tabs/AcademicRecords";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";
import { ArrowBackSharp, NoteSharp, PersonSharp, Report } from "@mui/icons-material";
import Content from "../components/Content";
import { getStudent } from "../components/api/studentApi";
import { getResult } from "../components/api/resultApi";
import { CustomContext } from "../components/context/provider/CustomProvider";
import CourseRegistrationTab from "../components/tabs/CourseRegistrationTab";
import { getAllRegisteredCourses } from "../components/api/registeredCoursesApi";
import { Helmet } from "react-helmet-async";

function StudentPersonalInfo() {
  const { customState, customDispatch } = useContext(CustomContext);
  const [tab, setTab] = useState("1");
  const [indexNumber, setIndexNumber] = useState(
    customState?.currentStudentIndexNumber
  );

  useEffect(() => {
    const idx = localStorage.getItem("indexNumber");
    if (idx !== null) {
      setIndexNumber(idx);
    }
  }, []);

  const studentData = useQuery(
    ["studentInfo", indexNumber],
    () => getStudent(indexNumber),
    {
      enabled: !!indexNumber,
      onSuccess: (student) => {
        customDispatch({ type: "studentInfo", payload: student });
      },
    }
  );

  const studentResult = useQuery(
    ["studentRecord", indexNumber],
    () => getResult(indexNumber),
    {
      enabled: !!indexNumber,
      onSuccess: (result) => {
        // //result)
        customDispatch({ type: "studentRecord", payload: result });
      },
    }
  );

  useQuery(
    ["studentCourse", indexNumber],
    () => getAllRegisteredCourses(indexNumber),
    {
      enabled: !!indexNumber,
      onSuccess: (courses) => {
        customDispatch({ type: "studentCourse", payload: courses });
   
      },
    }
  );

  // const StudentPersonalInfoTab = React.lazy(() =>
  //   import("../components/tabs/StudentPersonalInfoTab")
  // );
  return (
    <section className="-container">
      <Helmet>
        <title>AAMUSTED Results System | Student Info</title>
      </Helmet>
      <Content>
        {customState.user.role !== "student" && (
          <Link to="/info/student">
            <Button
              variant="contained"
              color="secondary"
              startIcon={<ArrowBackSharp />}
            >
              Go Back
            </Button>
          </Link>
        )}
        <TabContext value={tab}>
          <TabList
            onChange={(e, value) => setTab(value)}
            aria-label="Student Information"
            centered
          >
            <Tab
              value="1"
              label="Personal Information"
              icon={<PersonSharp />}
              iconPosition="start"
            />
            <Tab
              value="2"
              label="Course Registration"
              icon={<Report />}
              iconPosition="start"
            />
            <Tab
              value="3"
              label="Academic Records"
              icon={<NoteSharp />}
              iconPosition="start"
            />
          </TabList>
          <TabPanel value="1">
            <StudentPersonalInfoTab studentData={studentData.data} />
          </TabPanel>
          <TabPanel value="2">
            <CourseRegistrationTab />
          </TabPanel>

          <TabPanel value="3">
            <AcademicRecords
              studentResult={studentResult.data ? studentResult.data : []}
            />
          </TabPanel>
        </TabContext>
      </Content>
    </section>
  );
}

export default React.memo(StudentPersonalInfo);
